import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { SEARCH_LOGS_QUERY } from '@/graphql';

export default function useSearchLogsQuery() {
  const { result, loading, error, refetch } = useQuery(SEARCH_LOGS_QUERY);
  const searchLogs = useResult(result, [], (data: any) => data?.searchLogs);

  return {
    loading,
    error,
    refetch,
    searchLogs
  };
}
