import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { STICKS_QUERY } from '@/graphql';

interface Variables {
  place?: String;
  page?: Number;
  movies_page?: Number;
}
export default function useSticksQuery(variables?: Ref<Variables | undefined>, options?: UseQueryOptions) {
  const { result, loading, error, refetch } = useQuery(STICKS_QUERY, variables, options);
  const sticks = useResult(result, [], (data: any) => data?.sticks?.data);

  watch(variables, refetch);

  return {
    loading,
    error,
    refetch,
    sticks
  };
}
