<template>
  <div class="pwainstall-prompt">
    <el-drawer v-model="visible" direction="btt" :lock-scroll="true" :withHeader="false">
      <div class="pwa-prompt">
        <div class="prompt-header">
          <div class="app-info">
            <img src="@/assets/logo.png" class="logo-img" />
            <div>
              <p class="app-name">{{ name }}</p>
              <p class="app-host">{{ host }}</p>
            </div>
          </div>
          <button @click="buttonInstallClick" class="btn-install">安装</button>
        </div>
        <div class="prompt-body">
          <p v-if="description" class="app-description">
            {{ description }}
          </p>
          <div class="app-introduction">
            <ul class="flex-content">
              <li v-for="item in previews" :key="item" class="scroll-item">
                <img :src="item" class="preview" />
              </li>
            </ul>
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script lang="ts">
import { ref, computed } from 'vue';
import { ElNotification } from 'element-plus';

const PWAPrompte = (window.location.host + '-> Storage.pwaPrompt').toLocaleUpperCase();
const PWAPrompteType = {
  modal: 'modal',
  notification: 'notification'
};

function localStorageSetItem(key, value, maxAge?: number) {
  const VALUE = {
    value,
    maxAge,
    timestamp: Date.now()
  };
  localStorage.setItem(key, JSON.stringify(VALUE));
}

function localStorageGetItem(key) {
  let item: any = localStorage.getItem(key);
  try {
    item = JSON.parse(item);
  } catch (err) {}
  if (item && item.value) {
    if (item.maxAge && Date.now() - item.timestamp > item.maxAge * 1000) {
      localStorage.removeItem(key);
      return null;
    } else {
      return item.value;
    }
  }
}

export default {
  props: {
    name: {
      type: String,
      require: true
    },
    host: {
      type: String,
      default() {
        return window.location.host;
      }
    },
    description: {
      type: String,
      default() {
        return '点击“安装”按钮，添加快捷方式到桌面\n无广告、更轻量，随时发现更多精彩影视，解锁追剧新姿势。';
      }
    },
    previews: {
      type: Array,
      require: true
    },
    maxAge: {
      type: Number,
      default() {
        // 默认两个星期内不重复提示
        return 1209600;
      }
    }
  },
  beforeMount() {
    this.prompted = localStorageGetItem(PWAPrompte);
  },
  mounted() {
    if (!this.prompted && 'onbeforeinstallprompt' in window) {
      this.pwaEventListener();
    }
  },
  methods: {
    installNotification() {
      return ElNotification({
        duration: 10000,
        showClose: false,
        dangerouslyUseHTMLString: true,
        iconClass: 'logo',
        customClass: 'pwainstall-notification',
        message: `<div class="pwa-notification"><div class="pwa-notification__group"><div class="group-title">${this.name}</div><div class="group-content"><p>添加快捷方式到桌面，无广告、更轻量，随时发现更多精彩。</p></div></div><button class="pwa-notification__button" onclick="window.dispatchEvent(new CustomEvent('installpromptchoice'));">安装</button></div>`
      });
    },
    buttonInstallClick() {
      window.dispatchEvent(new CustomEvent('installpromptchoice'));
    },
    pwaEventListener() {
      let deferredPrompt;

      const installprompt = e => {
        e.preventDefault();
        deferredPrompt = e;
        this.showInstallPromotion();
        console.log(`'beforeinstallprompt' event was fired.`);
      };

      window.addEventListener('beforeinstallprompt', installprompt);

      window.addEventListener('installpromptchoice', async () => {
        console.log('await installpromptchoice');
        window.removeEventListener('beforeinstallprompt', installprompt);
        this.hideInstallPromotion();
        deferredPrompt.prompt();
        const { outcome } = await deferredPrompt.userChoice;
        console.log(`User response to the install prompt: ${outcome}`);
        if (outcome === 'dismissed') {
          localStorageSetItem(PWAPrompte, true, this.maxAge);
        } else {
          localStorageSetItem(PWAPrompte, true);
        }
        deferredPrompt = null;
      });

      window.addEventListener('appinstalled', () => {
        this.hideInstallPromotion();
        deferredPrompt = null;
        console.log('PWA was installed');
      });
    },
    showInstallPromotion() {
      if (this.promptType === PWAPrompteType.notification) {
        this.notify = this.installNotification();
      } else {
        this.visible = true;
      }
    },
    hideInstallPromotion() {
      this.notify?.close();
      this.visible = false;
    }
  },
  data() {
    return {
      visible: false,
      notify: null,
      promptType: window.screen.width >= 567 ? PWAPrompteType.notification : PWAPrompteType.modal
    };
  }
};
</script>

